import React, { Component } from 'react';
import './App.css';
import Keyboard from 'react-simple-keyboard';
import 'react-simple-keyboard/build/css/index.css';
import guessCorpus from './assets/words-source-curated5.txt';
import dictionary from './assets/words-dict-curated5.txt'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Snowfall from 'react-snowfall'
import Help from './Help';

class App extends Component {
  constructor() {
    super()
    this.myContainer = React.createRef()
    this.keyboard = React.createRef()
  }
  state = {
    guessCorpus: [],
    dictionary: [],
    guess: false,
    wordLength: 5,
    rowIndex: 0,
    colIndex: 0,
    inputs: [],
    over: false,
    won: false,
    lost: false,
    showHelp: false,
  }

  async loadWords() {
    fetch(guessCorpus)
      .then(r => r.text())
      .then(text => {
        const words = text.split('\n').filter(word => word.length === this.state.wordLength);
        console.log(`Read ${words.length} words`);

        this.setState({
          guessCorpus: words,
          guess: this.guessWord(words),
          inputs: this.formInputs()
        }, () => {
          fetch(dictionary)
            .then(r => r.text())
            .then(text => {
              const words = text.split('\n').filter(word => word.length === this.state.wordLength);
              this.setState({
                dictionary: [...words, ...this.state.guessCorpus]
              })
            })
        })
      });

  }

  componentDidMount() {
    this.loadWords()
    if (!window.localStorage.getItem('help-shown')) {
      this.setState({
        showHelp: true,
      }, () => {
        window.localStorage.setItem('help-shown', true)
      })
    }
  }

  guessWord(words) {
    return words[Math.floor(Math.random() * words.length)];
  }

  formInputs() {
    const inputs = []
    for (let i = 0; i <= this.state.wordLength; i++) {
      inputs.push(new Array(this.state.wordLength).fill(''))
    }
    return inputs;
  }

  restart() {
    const cells = document.getElementsByClassName('cell');
    for (let cell of cells) {
      cell.classList.remove('correct')
      cell.classList.remove('partial')
      cell.classList.remove('miss')
    }
    this.keyboard.removeButtonTheme('Q W E R T Y U I O P A S D F G H J K L M N B V C X Z', 'partial miss correct')
    this.setState({
      guess: this.guessWord(this.state.guessCorpus),
      inputs: this.formInputs(),
      rowIndex: 0,
      colIndex: 0,
      over: false,
      won: false,
      lost: false
    })
  }

  async checkWord() {

    const word = this.state.inputs[this.state.rowIndex].join('');
    console.log(this.state.rowIndex, word, '---')
    if (word.length < this.state.wordLength) {
      toast.warn('Word is too short!', {
        position: 'top-center',
        autoClose: 3000
      })
    } else if (this.state.dictionary.find(cand => cand === word)) {
      console.log('Word found in the dictionary')

      console.log('check and highilgiht')
      const cells = document.getElementsByClassName('cell');
      const row = this.state.rowIndex;
      const cellOffset = this.state.wordLength * row;
      const guess = this.state.guess
      const greens = new Set()
      const freqDict = (string =>
        string.split('').reduce((freqDict, char) => {
          freqDict[char] = (freqDict[char] || 0) + 1;
          return freqDict;
        }, {}))(guess)
      console.log(freqDict)
      const corrects = new Set();
      const partials = new Set();
      for (let cInd = 0; cInd < this.state.wordLength; cInd++) {
        const char = word[cInd]
        if (guess[cInd] === char) {
          corrects.add(cInd)
          freqDict[char] -= 1;
        }
      }
      for (let cInd = 0; cInd < this.state.wordLength; cInd++) {
        const char = word[cInd]
        if (guess.indexOf(char) !== -1 && freqDict[char] > 0) {
          partials.add(cInd)
          freqDict[char] -= 1;
        }
      }
      for (let cInd = 0; cInd < this.state.wordLength; cInd++) {
        const char = word[cInd]
        this.keyboard.removeButtonTheme(char, 'partial miss correct')
        if (corrects.has(cInd)) {
          cells[cellOffset + cInd].classList.add('correct')
          this.keyboard.addButtonTheme(word[cInd], 'correct')
          greens.add(char)
        } else if (partials.has(cInd)) {
          cells[cellOffset + cInd].classList.add('partial')
          this.keyboard.addButtonTheme(word[cInd], 'partial')
        } else {
          cells[cellOffset + cInd].classList.add('miss')
          this.keyboard.addButtonTheme(word[cInd], 'miss')
        }
      }

      if (this.state.guess === word) {
        this.setState({
          over: true,
          won: true
        })
      } else if (this.state.rowIndex < this.state.wordLength) {
        this.setState({
          rowIndex: this.state.rowIndex + 1,
          colIndex: 0,
        })
      } else {
        console.log('He lost')
        // toast.warn('You Lost', {
        //   position: 'top-center',
        //   autoClose: 3000
        // })
        this.setState({
          over: true,
          lost: true
        })
      }

    } else {
      console.log('not a dictionary word')
      toast.warn('Not a known word', {
        position: 'top-center',
        autoClose: 3000
      })
    }
  }

  onKeyPress = (button) => {
    console.log("Button pressed", button);
    if (this.state.over && button !== '{enter}') return;
    if (button === '{bksp}') {
      if (this.state.colIndex > 0) {
        const tmp = this.state.inputs
        tmp[this.state.rowIndex][this.state.colIndex - 1] = ''
        this.setState({
          inputs: tmp,
          colIndex: this.state.colIndex - 1
        })
      }
    } else if (button === '{enter}') {
      if (this.state.over) {
        this.restart()
      } else {
        this.checkWord()
      }
    } else {
      if (this.state.rowIndex <= this.state.wordLength && this.state.colIndex < this.state.wordLength) {
        const tmp = this.state.inputs
        tmp[this.state.rowIndex][this.state.colIndex] = button
        this.setState({
          inputs: tmp,
          colIndex: this.state.colIndex + 1
        })
      }
    }
  }

  get renderResultBlocks() {
    let result = ''
    const guess = this.state.guess;
    for (let i = 0; i < this.state.inputs.length; i++) {
      const word = this.state.inputs[i].join('')
      if (!word.length) break;
      for (let j = 0; j < word.length; j++) {
        if (word[j] === guess[j]) {
          result += ''
        } else if (guess.indexOf(word[j]) !== -1) {
          result += ''
        } else {
          result += ''
        }
      }
      result += '\n'
    }
    return (
      <button className='share-btn' onClick={() => {
        const title = "Wordle Unlimited"
        const message = `*Wordle Unlimited*\n\nI have guessed the word in Wordle Unlimited.\n${result}\n\nDo you want to try?\n`
        const url = "https://opensourcefeed.org/puzzles/wordle-unlimited/";
        if (window.navigator.share) {
          window.navigator.share({
            text: message,
            url: url,
            title: title,
          })
        } else {
          navigator.clipboard.writeText(`${message}${url}`)
          toast('Content has been copied to clipboard. You can paste and share now')
        }
      }}>{''}Share</button>)
  }

  get renderCells() {
    console.log(this, '-------------')
    const offsetWidth = (320) - this.state.wordLength * 20;
    const indices = []
    for (let i = 0; i <= this.state.wordLength; i++) {
      indices.push(i)
    }
    const rows = this.state.inputs.length ? indices.map(index => <tr>{indices.slice(0, this.state.wordLength).map(cInd => <td>
      <div className='cell' defaultValue={''} style={{
        height: offsetWidth / this.state.wordLength,
        width: offsetWidth / this.state.wordLength,
        fontSize: offsetWidth / this.state.wordLength - 8
      }}>{this.state.inputs[index][cInd]}</div></td>)}</tr>) : null
    return <table className='table input-cells'><tbody>
      {rows}
    </tbody>
    </table>
  }

  get randomColor() {
    const colors = ['red', 'yellow', 'white', 'orange']
    return colors[Math.floor(Math.random() * colors.length)]
  }

  render() {
    return (
      <div className="App" ref={this.myContainer}>
        <header>
          <table className='header-table'>
            <tbody><tr>
              <td className='help' style={{visibility: 'hidden'}}>?</td>
              <td className='title-wrapper'><h1 className="title">Wordle Unlimited</h1></td>
              <td style={{ textAlign: 'center' }}><span onClick={() => {
                this.setState({
                  showHelp: true
                })
              }} className='help'>?</span></td>
            </tr>
            </tbody>
          </table>
        </header>
        <hr />
        {/* I have read {this.state.words.length} words, so far!!.
        {this.state.guess && <div>I guess the word {this.state.guess}</div>} */}
        {this.renderCells}
        {this.state.won && <div className='message message-success'>
          <strong>Brilliant </strong><br />
          You have found the word<br />
          {this.renderResultBlocks}<br />
          <p className='underline'>Press Enter for another Game</p>
        </div>
        }
        {this.state.lost && <div className='message message-failure'><strong>You Lost! </strong><br /> The word was <strong>{this.state.guess}</strong>. <br />
          <p className='underline'>Press Enter for another Game</p>
        </div>}
        <div className='keys' style={{ visibility: this.state.showHelp ? 'hidden' : 'visible' }}>
          <Keyboard
            keyboardRef={r => this.keyboard = r}
            onKeyPress={this.onKeyPress.bind(this)}
            layoutName='default'
            layout={{
              'default': [
                'Q W E R T Y U I O P',
                'A S D F G H J K L',
                '{enter} Z X C V B N M {bksp}',
              ]
            }}
            display={{
              '{enter}': 'ENTER',
              '{bksp}': '&lArr;'
            }}
            physicalKeyboardHighlight={true}
            physicalKeyboardHighlightPress={true}
          />
        </div>
        <ToastContainer />
        {this.state.won && <Snowfall color={this.randomColor} />}
        {this.state.showHelp && <Help secret={this.state.guess} onCloseRequest={() => {
          this.setState({
            showHelp: false
          })
        }} />}
      </div>
    );
  }
}

export default App;



